@import url(https://fonts.googleapis.com/css2?family=Courgette&family=Merienda:wght@700&display=swap);
:root {
	--text-primary: #d44343;
	--lightgreen: lightgreen;
	--lightergreen: #dbf0e1;
	--lightyellow: #fff2cc;
}
.reward {
	height: 70px;
	width: 320px;
	border: 1px dashed gray;
	/*border-radius: 5em;*/
	padding: 0.6em;
	/*margin: 0 auto;*/
	font-weight: 900;
	font-size: 1em;
	display: inline-block;
	text-align: center;
	vertical-align: middle;
	color: #000;
	background-color: #fff2cc;
	background-color: var(--lightyellow); /* #fff2cc */ /*#dbf0e1*/	
	/*margin-left: 2em;*/
}
/*.reward-unlocked::after {
	content: 'CLAIM REWARD';
}*/
.reward-active {
	/*content: 'CLAIMING...';*/
	background-color: #dbf0e1;
	background-color: var(--lightergreen);
	border: 5px solid lightgreen;
	border: 5px solid var(--lightgreen);
	color: green !important;
/*	background-color: #fff2cc;	
	border: 5px solid gold;*/
}
.reward-unlocked {
	color: #3f3d3d;
}
.reward-locked {
	color: darkgray;
	border: 1px dashed darkgray;	
	background-color: #eeeeee;
}
.message {
	padding: 0.3em 0;
	/*background-color: #fff2cc;*/
	width: 100%;
	/*margin: 3em auto;*/
	color: #39b20e;	/* #e26917 */
	position: relative;
	margin-left: auto;
	/*top: 5em;*/
	float: right;
}
.gray {
	color: darkgray;
	border: 1px dashed darkgray;
}
.dark-green {
	color: darkgreen;
}
.label-redeem {
	color: #e26917;
	font-size: 0.9em;
	text-transform: uppercase;
	display: block;
	margin: 0 auto;
}
.label-ineligible {
	color: #ea8286;
}

.icon-circle {
	color: darkorange;
	display: inline-block;
	/*padding-right: 0.2em;*/
	font-size: 1.2em;
	margin-top: 0.3em;
	/*vertical-align: middle;*/
}

#minus-circle {
	color: darkgray;
}

.text-bold {
	font-weight: 900;
}
.text-center {
	text-align: center;
}
.show {
	display: block;
}
.hide {
	display: none;
}
.fade {	
	/*opacity: 0;*/
    /*transition: all 250ms linear 1s;*/
  -webkit-animation: fadeout 1s linear forwards;
  /*animation: fadein 0 linear forwards;*/
  animation: fadeout 1s linear forwards;
  /*opacity: 0;*/
}
#flower-emoji {
	font-size: 0.7em;
}
#title {
	/*font-family: 'Courgette';*/
	font-family: 'Marker Felt';
	/*letter-spacing: 0.01em;*/
	font-size: 2.3em;
	color: #d44343;
	color: var(--text-primary);
	/*font-weight: 900;*/
}
p {
	font-size: 1.2em;
}

@-webkit-keyframes fadein {
  10% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }  
}

@keyframes fadein {
  10% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }  
}

@-webkit-keyframes fadeout {
  50% {
    /*opacity: 1;*/
    height: 2em;
  }
/*  95% {
    opacity: 0;
    height: 2em;
  } */ 
  100% {
    /*display: none;*/
    opacity: 0;
    height: 0;
  }
}

@keyframes fadeout {
  50% {
    /*opacity: 1;*/
    height: 2em;
  }
/*  95% {
    opacity: 0;
    height: 2em;
  } */ 
  100% {
    /*display: none;*/
    opacity: 0;
    height: 0;
  }
}
#btn {
	display: inline-block;
	bottom: 0.5em;
	right: 3em;
	padding: 0.2em 0.5em;
	position: fixed;
	font-weight: 900;
	background-color: #E6D0DC;
	border: 1px solid #cf3030;
	height: 2.3em;
	font-size: 0.85em;
	letter-spacing: 0.08em;
	color: blueviolet;
	border-radius: 0.2em;
}
.em {
	border-bottom: 2px solid lightgreen;
	font-weight: 900;
}
.spacing {
	padding: 1em;
}
.actionsBar {
    box-shadow: 'none';
    border-bottom: 1px solid rgba(0, 0, 0, 0.12);
    height: 3em;
    width:  100%;
    position: fixed;
    bottom: 0;
    background-color: #facfca;
    display: table-cell;
    padding: 1em;
    font-size: 1.15em;
    font-weight: 600;
}
.info {
    margin-top: -1.5em;
    margin-left: -2em;
    text-align: center;
    color: #cf3030;
}
.birthdayReward {
    margin-right: 5em !important;
    display: inline-block;
    /*padding-right: 5em !important;*/
}
